import { Component, OnInit } from '@angular/core';
import { AppointmentService } from '../core/appointment.service';
import { UtilityService } from '../core/utility.service';
import * as moment from 'moment';
import { CalendarComponentOptions } from 'ion2-calendar';
@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.page.html',
  styleUrls: ['./calendar.page.scss'],
})
export class CalendarPage implements OnInit {
  data;
  date: string;
  type: 'string';
  role;
  selectedDate = moment();
  optionsMulti: CalendarComponentOptions = {
    pickMode: 'multi',
  };
  constructor(
    private utility: UtilityService,
    private appointment: AppointmentService
  ) {}

  ngOnInit() {
    this.role = this.utility.getRole();
    this.getAppointments();
  }

  getAppointments() {
    this.utility.present();
    if (this.role == 'pet owner') {
      this.appointment.getUserAppointments(this.selectedDate).subscribe(
        (res) => {
          this.data = res;
          console.log(this.data);
          this.utility.dismiss();
        },
        (err) => {
          this.utility.dismiss();
          console.log(err, 'error');
        }
      );
    } else {
      this.appointment
        .getProviderAppointment(this.selectedDate)
        .subscribe((res) => {
          this.data = res;
          console.log(this.data);
          this.utility.dismiss();
        });
    }
  }

  onChange($event) {
    console.log($event);
    let date = $event.toString();
    console.log(date);
    this.selectedDate = date;
    console.log(this.selectedDate);
    this.getAppointments();
  }
}
